import Vue from 'vue'
import Router from 'vue-router'
import Home from './views/Home.vue'
import Projects from './views/Projects.vue'
import Resume from './views/Resume.vue'


Vue.use(Router)

export default new Router({
  mode: 'hash',
  base: 'process.env.BASE_URL',
  routes: [
    {
      path: '/',
      name: 'home',
      component: Home
    },
    {
      path: '/projects',
      name: 'projects',
      component: Projects
    },
    {
      path: '/resume',
      name: 'resume',
      component: Resume
    },
    { path: '*', component: Home }
  ]
})
